<template>
    <a-layout class="main_page">
        <a-layout-header style="background-color: #00BFFF"> <h1>{{ msg }}</h1> </a-layout-header>
        <a-layout-content>            
            <a-row justify="center">
               
                <a-col :span="18" >
                    <div class="controlPanel" style="display: flex; justify-content: center;">
                    <a-card title="Parameters Setting For Deep Learning in Databases" 
                        :bordered="false" 
                        class="cardContainer">
                    <!-- <div class="selectionContainer"> -->
                    
                    <template v-if="!isTrain">
                    <div class="selection">
                        <span class="Option_data_title">Choose a Model: </span>
                        <a-cascader
                            v-model="selectModel"
                            @change="changeModel"
                            :options="modelStrings"
                            placeholder="Select a model."
                            >
                        </a-cascader>
                    </div>

                    <div class="selection">
                        <span class="Option_data_title">Choose a dataset: </span>
                        <a-cascader
                            v-model="selectData"
                            @change="changeDataSet"
                            :options="dataStrings"
                            placeholder="Select a dataset."
                        ></a-cascader>                    
                    </div>

                    <div class="selection">
                        <span class="Option_query_title">Choose base relation number: </span>
                        <a-cascader 
                            v-model="selectQuery"
                            @change="changeQuery"
                            :options="queryStrings"
                            placeholder="Select query set."
                        ></a-cascader>                    
                    </div>
                    <div class="selection">
                        <span class="Option_query_num_title">Choose the number of queries: </span>
                        <a-cascader
                            v-model="selectQueryNum"
                            @change="changeQueryNum"
                            :options="querynumStrings"
                            placeholder="Select query number."
                        ></a-cascader>                 
                    </div>
                    <div class="buttonContainer">
                        <a-button type='primary' @click="run" >Run Model</a-button>
                        <a-button style="margin-right: 6px; margin-left: 6px" @click="viewTutorial">
                           {{ showTutorial ? "Close" : "View Tutorial" }}
                        </a-button> 
                        <a-button danger ghost @click="clean">Clean Data</a-button> 
                        <a-button type='primary' @click="switchMode">Training</a-button>  
                        <!-- <a-button @click="run4">run4</a-button> -->
                    </div>
                    </template> 
                    <template v-else>
                            <div>
                            <a-progress type="circle" :percent="75" :format="percent => `${percent} Days`" />
                            <a-progress type="circle" :percent="100" :format="() => 'Done'" />
                            </div>
                            <a-button type='primary' @click="switchMode">Run Model</a-button>
                    </template>  
                    <div>
                        <a-divider style="background-color: #00BFFF" ></a-divider>
                        <!-- possible Tutorial -->
                        <template v-if="showTutorial">
                            <a-steps v-model:current="currentStep">
                                <a-step title="Step 1" description="Choosing Parameter" />
                                <a-step title="Step 2" description="Confirm to run" />
                                <a-step title="Step 3" description="Retreiving result" />

                            </a-steps>
                            <div class="stepContent">
                                <a-descriptions title="Selecting Parameters like following" v-if="currentStep === 0">
                                    <a-descriptions-item>
                                        <div class="selection">
                                            <span class="Option_data_title">Choose a Model: </span>
                                            <a-select>
                                            </a-select>
                                        </div>
                                        <div class="selection">
                                            <span class="Option_data_title">Choose a dataset: </span>
                                            <a-select-option value="disabled" disabled>IMDB</a-select-option>                
                                        </div>
                                    </a-descriptions-item>
                                </a-descriptions>

                                <a-descriptions v-else-if="currentStep === 1">
                                    <a-descriptions-item >
                                        Click confirm to continue
                                    </a-descriptions-item>
                                </a-descriptions>

                                <a-descriptions v-else>
                                    <a-descriptions-item >
                                        ending
                                    </a-descriptions-item>
                                </a-descriptions>
                            </div>
                        </template>
                    </div>

                    </a-card>
                    </div>


                    <a-divider style="background-color: #00BFFF" ></a-divider>

                    <a-modal v-model:visible="modalVisible" title="Model Running" ok-text="confirm" cancel-text="cancel" @ok="runModel">
                    <p>
                        You are running model <span class="highlightText">{{selectModel}}</span>, 
                        with dataset <span class="highlightText">{{selectData}}</span>,
                        joining <span class="highlightText">{{selectQuery}}</span> tables 
                        with <span class="highlightText">{{selectQueryNum}}</span> query.
                    </p>
                    </a-modal>

                    <!-- show schema -->
                    <template v-if="selectData == 'IMDB'">
                        <div class="schemeDisplay">
                            <span>Dataset: {{ this.selectData }}</span>
                            <a-button @click="generateImage(); avgImage(); planImage(); execImage()"> image</a-button>
                            <span>{{ checked ? "Hide" : "Show" }} Schema <a-switch v-model:checked="checked" style="margin-left: 12px;"/></span>
                        </div>
                        <a-table v-if="checked" :columns="tableSchema"  :dataSource="ImdbSchema" :pagination=false >
                        </a-table>
                        <br/>
                        <a-image
                            v-if="checked"
                            :width="750"
                            src="http://137.189.59.166:8000/imdb.png"
                        />
                    </template>

                    <template v-else-if="selectData === 'TPCDS'">
                        <div class="schemeDisplay"> 
                            <span>  Dataset: {{ this.selectData }}</span>
                            <span>{{ checked ? "Hide" : "Show" }} Schema<a-switch v-model:checked="checked" style="margin-left: 12px;"/></span>
                        </div>
                        <a-table v-if="checked" :columns="tableSchema" :dataSource="TpcdSchema" :pagination=false >
                        </a-table>
                        <br/>
                        <a-image
                            v-if="checked"
                            :width="750"
                            src="http://137.189.59.166:8000/tpcds.png"
                        />
                    </template>
                    <template v-else><span>Schema will be shown when you select the dataset</span></template>

                    <a-divider style="background-color: #00BFFF" ></a-divider>
                    <!-- response data into table form -->
                    <template v-if="isShow">

                        <p>
                        Result of running model <span class="highlightText">{{selectModel}}</span>, 
                        with dataset <span class="highlightText">{{selectData}}</span>,
                        joining <span class="highlightText">{{selectQuery}}</span> tables 
                        with <span class="highlightText">{{selectQueryNum}}</span> query.
                        <a-button @click="generateImage();"> image</a-button>
                        </p>
                        <template v-if="isImageShow">
                        <a-image
                            :height="250"
                            :width="550"
                            :src="var_avgImg"
                        />
                        <a-image
                            :height="250"
                            :width="550"
                            :src="var_planImg"
                        />
                        <a-image
                            :height="250"
                            :width="550"
                            :src="var_execImg"
                        />
                        </template>
                        <a-table :columns="reponseDataColumns" :dataSource="displayData" @change="headerChange" >
                            <template #bodyCell="{ column }">
                                <template v-if="column.key === 'operation'"><a>action</a></template>
                            </template>
                        </a-table>

                    </template>

                </a-col>

            </a-row>
            <a-back-top />
        </a-layout-content>

    <a-layout-footer>
    </a-layout-footer>

    </a-layout>
</template>


<script>
import { modelStrings, dataStrings, queryStrings, querynumStrings, tableSchema, ImdbSchema, TpcdSchema, reponseDataColumns, ForestSchema } from '@/data/optionstr';
import { GUIService } from '@/services/services';
import { http } from '@/services/http_common';
import axios from "axios";


export default {
    name: "SelectComponent",
    data() {
        return {
            // import items: 
            modelStrings,
            dataStrings,
            queryStrings,
            querynumStrings,
            tableSchema,
            ImdbSchema,
            TpcdSchema,
            ForestSchema,
            // selection control:
            selectModel: 'NNGP',
            selectData: '',
            selectQuery: '',
            selectQueryNum: '',
            isShow: false,
            displayData: "", // json data 
            desciption: '',
            reponseDataColumns,
            click_msg: "SEEM FYP Project",
            modalVisible: false, // show confirm dialogue
            checked: true, // showing schmea or not 
            var_imgSource4: "http://137.189.59.166:8000/image1.png",
            var_avgImg:"",
            var_planImg:"",
            var_execImg:"",
            isImageShow: false,
            schemea: "Schema will be shown here", // if not selected schema,
            showTutorial: false,
            currentStep: 0,
            isTrain: false,
        }
    },
    components: {
    },
    computed: {
        msg() {
            return this.click_msg
        },
        imgSource4() {
            return this.var_planImg;
        },
        avgImage() {
            console.log("triggered as the average image link changed")
            return this.var_avgImg;
        },
        planImage() {
            return this.var_planImg;
        },
        execImage() {
            return this.var_execImg;
        }
    },
    methods: {
        run4() {
            GUIService.get4().then(response => { 
                // this.imgSource4 = "data:image/png;base64," + response.data;
                this.var_imgSource4 = "";
                this.var_imgSource4 = response.data;
                console.log("From VUE: Response = " + this.var_imgSource4);
                console.log("From VUE: url = " + this.imgSource4);
            }).catch(e => {console.log(e);});
        },
        clean() {
            this.isShow = false;
            GUIService.getClean().catch(e => {console.log(e); });
        },
        runModel(){
            console.log("selectData: " + this.selectData)
            console.log("selectQuery: " + this.selectQuery)
            console.log("selectQueryNum: " + this.selectQueryNum)
            console.log(this.TpcdSchema)
            if ( this.selectData && this.selectQuery && this.selectQueryNum) {
                http.get(`/runModel/${this.selectData}-${this.selectQuery}-${this.selectQueryNum}`)
                .then(response => {
                    console.log(response.data);
                    this.displayData = response.data; // json data 
                    this.isShow = true;
                }).catch(e => { console.log(e); })
                this.modalVisible = false;
                console.log("running model")
            } else {
                this.modalVisible = false;
                window.alert("Please select all parameters");
            }
        },
        // onChange methods of selecting params
        changeModel(value) {
            this.selectModel = value;
            console.log("selectModel: "+this.selectModel);
        },
        changeDataSet(value){
            this.selectData = value[0];
            console.log(this.selectData)
            if (this.selectData === "IMDB") {
                    console.log("using imdb")
                    this.schema = ImdbSchema
            }   
            else {
                console.log("using tbcds")
                this.schemea = TpcdSchema
            }
        },
        changeQuery(value){
            this.selectQuery = value;
        },
        changeQueryNum(value) {
            this.selectQueryNum = value;   
        },
        run() {
            this.modalVisible = true;
        },
        headerChange: (pagination, filters, sorter) => {
            this.displayData = this.displayData.sort()
            console.log("params: " , pagination, filters, sorter); 
        },
        viewTutorial() {
            if (this.showTutorial){
                this.showTutorial = false; 
            } else {
                this.showTutorial = true;
            }
        },
        // generation image 
        generateImage(){
            const planningtime = this.displayData.map(data => data.Planning);
            const nngpPlanning = this.displayData.map(data => data.NNGP_Planning);
            const execution = this.displayData.map(data => data.Execution);
            const nngpExecut = this.displayData.map(data => data.NNGP_Execution);
            axios.post(
                'api/image',
                {   
                    plan: planningtime,
                    execution: execution,
                    nngp_plan: nngpPlanning,
                    nngp_execution: nngpExecut
                },
            ).then((response) => {
                this.isImageShow = false;
                this.var_avgImg = "";
                this.var_execImg = "";
                this.var_planImg = "";

                let datasoruce = response.data;
                this.var_avgImg = datasoruce.average;
                this.var_execImg = datasoruce.execution;
                this.var_planImg = datasoruce.planning;
                this.isImageShow = true;

                // http://137.189.59.166:8000/AVB_Comparsion.png
                // http://localhost:8000/AVB_Comparsion.png
                console.log(response)
            }).catch(error => { console.log(error) })
        },
        switchMode(){
            if (this.isTrain) {
                this.isTrain = false;
            } else {
                this.isTrain = true;
            }
        }
    }
}
</script>

<style>
h1 {
    color: white;
}
.highlightText{
    color: red;
}
span {
    font-size: 16px;
}
p{
    font-size: 16px;
}
.selectionContainer{
    display: block;
    margin-top: 5px;
}
.buttonContainer{
    margin-top: 6px;
    display: flex;
    justify-content: center;
}
.schemeDisplay{
    display: flex;
    justify-content: space-between;
}
.cardContainer{
    width: 80%; 
    margin-top: 10px; 
    justify-items: center;
}
.Popup
{
    top: 50%;
    left: 50%;
    position: absolute;
    z-index:50;
}

</style>


